.tarea-contenedor2{
    width: 570px;
    min-height: 65px;
    background-color: #1b1b32;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border:  1px solid white;
    cursor: pointer;
}
.tarea-contenedor2:nth-child(3n + 1){
   background-color: #ffffff;
   border: 1px solid rgb(172, 175, 1);
}
.tarea-contenedor2:nth-child(3n + 2){
   background-color: #ffffff;
   border: 1px solid rgb(7, 23, 250);
}
.tarea-contenedor2:nth-child(3n + 3){
   background-color: #ffffff;
   border: 1px solid rgb(253, 0, 0);
}
.tarea-texto2{
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
    user-select: none;
}
.tarea-icono {
    width:  25px;
    height: 25px;
}
.tarea-icono:hover{
    color: red;
}

.tarea-contenedor2.completada2{
    background-color: rgb(208, 251, 235);
}
